<template>
  <div
    v-click-outside="hideDropdown"
    class="dropdown"
    :style="{
      '--dropdown-body-width': bodyWidth
    }"
    @click="showDropdown"
  >
    <div
      class="dropdown__label"
      @click.stop="toggleDropdown"
    >
      <slot name="label" />
    </div>
    <div
      v-show="isShowDropdown"
      ref="body"
      class="dropdown__body"
      :style="position"
      @click.stop="hideOnClick && hideDropdown()"
    >
      <slot name="body" />
    </div>
  </div>
</template>

<script>
import { computed, onMounted, ref } from 'vue'
import clickOutside from '@/directives/clickOutside'
import { checkViewPort } from '@/utils/checkViewPort'

export default {
  name: 'Dropdown',
  directives: {
    clickOutside
  },
  emits: ['update:modelValue'],
  props: {
    bodyWidth: {
      type: String,
      default: 'unset'
    },
    modelValue: {
      type: [Boolean, null],
      default: null
    },
    hideOnClick: {
      type: Boolean,
      default: false
    }
  },
  setup (props, { emit }) {
    const isShow = ref(false)
    const body = ref()
    const view = ref({})
    const isShowDropdown = computed({
      get: () => props.modelValue === null ? isShow.value : props.modelValue,
      set: (val) => {
        isShow.value = !!val
        emit('update:modelValue', !!val)
      }
    })
    const position = computed(() => {
      // const { value } = view
      const styles = {}
      return styles
    })

    const toggleDropdown = () => {
      isShowDropdown.value = !isShow.value
    }
    const hideDropdown = () => {
      isShowDropdown.value = false
    }
    const showDropdown = () => {
      isShowDropdown.value = true
    }

    onMounted(() => {
      view.value = checkViewPort(body.value)
    })

    return {
      isShowDropdown,
      body,
      position,
      view,
      showDropdown,
      hideDropdown,
      toggleDropdown
    }
  }
}
</script>

<style lang="scss">
.dropdown {
  position: relative;
  display: inline-block;

  &__label {
    cursor: pointer;
    height: 100%;
  }
  &__body {
    @include position-align-center;
    top: calc(100% + 16px);
    z-index: 10;
    width: var(--dropdown-body-width);
  }
}
</style>
