<template>
  <div class="date-picker" @click="pickerFocus" v-click-outside="pickerBlur">
    <Input
      v-model="inputValue"
      :placeholder="placeholder"
      class="date-picker__input"
      hide-shadow
    >
      <template #prepend>
        <Icon name="calendar" />
      </template>
    </Input>
    <v-date-picker
      v-show="showPicker"
      v-model="inputValue"
      ref="datePicker"
      :columns="2"
      color="orange"
      :first-day-of-week="1"
      :masks="{
          title: 'MMM YYYY',
          weekdays: 'WWW',
          input: 'DD/MM/YYYY'
        }"
      :is-range="isRange"
      class="date-picker__picker"
    />
  </div>
</template>

<script>
import Input from '@/components/UIKit/Input'
import Icon from '@/components/Icon/Icon'
import clickOutside from '@/directives/clickOutside'
import { ref } from 'vue'
import { useModel } from '@/utils/useModel'
import moment from 'moment'

export default {
  name: 'DatePicker',
  components: { Icon, Input },
  directives: {
    clickOutside
  },
  emits: ['update:modelValue', 'blur', 'focus'],
  props: {
    isRange: {
      type: Boolean,
      default: false
    },
    minDate: {
      type: [Date, String, null],
      default: ''
    },
    maxDate: {
      type: [Date, String, null],
      default: ''
    },
    modelValue: {
      type: [Date, String],
      default: ''
    },
    placeholder: {
      type: String,
      default: ''
    }
  },
  setup (props, { emit }) {
    const showPicker = ref(false)
    const { value: inputValue } = useModel(
      props,
      emit,
      (val) => moment(val).format('DD/MM/YYYY')
    )

    const toDate = (val) => val ? new Date(val) : null
    const pickerFocus = () => {
      emit('focus')
      showPicker.value = true
    }
    const pickerBlur = () => {
      emit('blur')
      showPicker.value = false
    }
    return {
      inputValue,
      showPicker,
      pickerBlur,
      pickerFocus,
      toDate
    }
  }
}
</script>

<style lang="scss">
.date-picker {
  position: relative;
  height: 100%;
  width: 50%;

  &__input {
    height: 100%;
  }
  &__picker {
    position: absolute;
    z-index: 10;
    right: 0;
    top: calc(100% + 5px);
  }
}
</style>
