<template>
  <div class="date-range">
    <DatePicker
      v-model="dateFrom"
      :max-date="dateTo"
      :placeholder="placeholderFrom"
      class="date-range__field"
    />
    <DatePicker
      v-model="dateTo"
      :min-date="dateFrom"
      :placeholder="placeholderTo"
      class="date-range__field"
    />
  </div>
</template>

<script>
import DatePicker from '@/components/UIKit/DatePicker'
import { computed } from 'vue'

export default {
  name: 'DateRange',
  emits: ['update:modelValue'],
  components: { DatePicker },
  props: {
    modelValue: {
      type: Object,
      default: () => ({})
    },
    placeholderFrom: {
      type: String,
      default: ''
    },
    placeholderTo: {
      type: String,
      default: ''
    }
  },
  setup (props, { emit }) {
    const dateFrom = computed({
      get: () => props.modelValue.from,
      set (val) {
        emit('update:modelValue', {
          ...props.modelValue,
          from: val
        })
      }
    })
    const dateTo = computed({
      get: () => props.modelValue.to,
      set (val) {
        emit('update:modelValue', {
          ...props.modelValue,
          to: val
        })
      }
    })

    return {
      dateFrom,
      dateTo
    }
  }
}
</script>

<style lang="scss">
.date-range {
  display: flex;
  align-items: center;

  &__field:first-child {
    margin-right: 8px;
  }
}
</style>
